.experience{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 6rem;
    position: relative;
    top: -8rem;
}

.achievement{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.circle{
    height: 4rem;
    width: 4rem;
    background: white;
    border-radius: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 2rem;
}

.circle::before{
    border-radius: 100%;
    content: "";
    background-image: linear-gradient(to bottom, #87e6fb 0%, #ffc05c 100%);
    top: -8px;
    left: -8px;
    bottom: -8px;
    right: -8px;
    position: absolute;
    z-index: -1;
    box-shadow: var(--boxShadow);
}

.achievement span:nth-of-type(2){
    color: var(--blue);
}